[data-theme=light]{--bg-primary: #FFFFFF;--bg-secondary: #E5E7EB;--text-primary: #333333;--text-secondary: #818181;--accent-primary: #3B82F6;--accent-primary-darken: #2563EB;--accent-secondary: #F97316;--accent-secondary-darken: #EA580C;--border-color: #818181}[data-theme=dark]{--bg-primary: #121212;--bg-secondary: #1E1E1E;--text-primary: #E5E7EB;--text-secondary: #818181;--accent-primary: #3B82F6;--accent-primary-darken: #2563EB;--accent-secondary: #F97316;--accent-secondary-darken: #EA580C;--border-color: #818181}.navigation{background-color:var(--bg-secondary);box-shadow:0 0 5px 0 var(--border-color);color:var(--text-primary);transition:all .3s ease-in-out;z-index:2;position:sticky;display:flex;align-items:flex-start;flex-direction:row;gap:1rem;justify-content:center;padding:.5rem;max-height:45px}.navigation_search{display:flex}.navigation_search select{border-right:1px solid var(--border-color)}.navigation .auth{padding:0 5px;text-decoration:none}